<template>
	<div class="banned">
		<!--主页-->
		<div class="list">

			<p>禁言名单</p>
			<ul>
				<!---->
				<li class="bannedList_li" v-for="item in infoList">
					<a href="#" ref="listA">
						<span>
						<img  :src="item.headimgurl" /></span>
						<span class="user_name">{{item.nickname}}</span>
						<i @click="delbanned(item.Uid)" :data-uid="item.Uid">删除</i>
					</a>
				</li>

			</ul>
		</div>
		<!--弹框-->
		<div class="banned_mask" v-if="toggle">
			<div>
				<p>确定要从禁言名单中</p>
				<p>移除该学员吗</p>
			</div>
			<p class="displayflex"><span @click="clickcancel">取消</span>
				<span class="sure" @click="clicksure">确认</span>
			</p>
		</div>
		<!--底部-->
		<div class="banned_bottom" @click="totalbanned">
			<p v-if="chooselbanned">全部禁言</p>
			<p v-else>取消全部禁言</p>
		</div>

		<v-mask :flag="toggle"></v-mask>
	</div>
</template>

<script>
	// 引用遮罩层组件
	import mask from '../functionComponents/mask'
	const ERR_Ok = 0;
	export default {
		name: 'banned',
		data() {
			return {
				toggle: false,
				chooselbanned: true,
				infoList: [],
				uid: "",
				state:"",
				
			}
		},

		// 实例挂载完毕的回调函数
		mounted() {
			//获取禁言用户列表
			this.$http.get('http://120.24.6.91:8082/wx/powderSystem/getIsProhibitUserList?groupId='+userObj.groupId).then((response) => {
				response = response.body;
				this.infoList = response;
				this.$nextTick(() => {
					//侧滑显示删除按钮
					var expansion = null; //是否存在展开的list
					var container = this.$refs.listA;
					for(var i = 0; i < container.length; i++) {
						var x, y, X, Y, swipeX, swipeY;
						container[i].addEventListener('touchstart', function(event) {
							x = event.changedTouches[0].pageX;
							y = event.changedTouches[0].pageY;
							swipeX = true;
							swipeY = true;
							if(expansion) { //判断是否展开，如果展开则收起
								expansion.className = "";
							}
						});
						container[i].addEventListener('touchmove', function(event) {

							X = event.changedTouches[0].pageX;
							Y = event.changedTouches[0].pageY;
							// 左右滑动
							if(swipeX && Math.abs(X - x) - Math.abs(Y - y) > 0) {
								// 阻止事件冒泡
								event.stopPropagation();
								if(X - x > 10) { //右滑
									event.preventDefault();
									this.className = ""; //右滑收起
								}
								if(x - X > 10) { //左滑
									event.preventDefault();
									this.className = "swipeleft"; //左滑展开
									expansion = this;
								}
								swipeY = false;
							}
							// 上下滑动
							if(swipeY && Math.abs(X - x) - Math.abs(Y - y) < 0) {
								swipeX = false;
							}
						});
					}
				});

			})

		},
		components: {
			'v-mask': mask,

		},
		methods: {
			clicksure: function() {
				this.toggle = !this.toggle;
				this.$http.get('http://120.24.6.91:8082/wx/powderSystem/cancelIsProhibitUser?groupId='+userObj.groupId+'&uid=' + this.uid).then((response) => {
					response = response.body;
					for(let i in this.infoList) {
						if(this.infoList[i].Uid == this.uid) {
							this.infoList.splice(i, 1);
						}
					}
					console.log(response);

				})
			},
			clickcancel: function() {
				this.toggle = !this.toggle;
			},
			delbanned: function(uid) {
				event.preventDefault();
				this.toggle = !this.toggle;
				this.uid = uid;
				console.log(this.uid);
			},
			totalbanned: function() {
				this.chooselbanned = !this.chooselbanned;
				if(this.chooselbanned==true){
					this.state=0;
					this.$http.get("http://120.24.6.91:8082/wx/powderSystem/edGroupIsProhibitState?groupId="+userObj.groupId+"&state="+this.state).then((response) => {
						console.log(response.body);
						// 取消全体禁言
						let obj = {
							Uid: userObj.uid,
							banned: false,
							dataType: 44
						};
						MessageSend(JSON.stringify(obj));
					});
				}else{
					this.state=1;
					this.$http.get("http://120.24.6.91:8082/wx/powderSystem/edGroupIsProhibitState?groupId="+userObj.groupId+"&state="+this.state).then((response) => {
						console.log(response.body);
						let obj = {
							Uid: userObj.uid,
							banned: true,
							dataType: 44
						};
						MessageSend(JSON.stringify(obj));
						console.log('全体禁言');
					});
				}
			}

		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/css/var.scss';
	.bannedList_li {
		img {
			height: 40px;
			width: 40px;
			vertical-align: middle;
		}
	}
	
	.list {
		overflow: hidden;
		border-top: 1px solid #ddd;
		.user_name {
			margin-left: 8px;
		}
		span {
			vertical-align: middle;
		}
		p {
			height: 25px;
			line-height: 25px;
			margin-left: 10px;
		}
		i {
			font-style: normal;
		}
		ul {
			border-top: 1px solid $bordercolor;
			background-color: $whitecolor;
		}
		li {
			overflow: hidden;
			width: 120%;
			border-bottom: 1px solid #ddd;
			height: 60px;
			line-height: 60px;
			margin-left: 3%;
		}
		a {
			display: block;
			height: 60px;
			-webkit-transition: all 0.3s linear;
			transition: all 0.3s linear;
			color: $txtcolor;
		}
		i {
			float: right;
			width: 80px;
			text-align: center;
			background: $bordercolor;
			color: #fff;
			text-align: center;
		}
		.swipeleft {
			transform: translateX(-15%);
			-webkit-transform: translateX(-15%);
		}
	}
	
	.banned_mask {
		width: 70%;
		height: 140px;
		background-color: #fff;
		border-radius: 4px;
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		margin: auto;
		z-index: 11;
		padding-top: 25px;
		text-align: center;
		box-sizing: border-box;
		p {
			height: 22px;
			line-height: 22px;
		}
		div {
			border-bottom: 1px solid $bordercolor;
			padding-bottom: 25px;
		}
		.displayflex {
			display: flex;
			height: 45px;
			line-height: 45px;
			margin-right: -1px;
			span {
				flex: 1;
			}
			span.sure {
				border-left: 1px solid $bordercolor;
				color: $bluecolor;
			}
		}
	}
	
	.banned_bottom {
		position: fixed;
		width: 100%;
		bottom: 0;
		height: 55px;
		line-height: 55px;
		color: $whitecolor;
		background-color: $txtcolor;
		font-size: $font16;
		text-align: center;
	}
</style>


