<template>
  <div class="enterExamationResult" v-if="isExamationResult">
	 <router-link :to="{path: 'examinationResult'}">
			<span class="text">点击进入5.17每日一练查看结果</span>
	 </router-link>
		<span class="close" @click.prev="closeExamationResult">
			<i class="iconfont icon-close01"></i>
		</span>
  </div>
</template>

<script>
export default {
  name: 'enterExamationResult',
  data () {
    return {
      isExamationResult: true
    }
  },
  methods: {
  	closeExamationResult() {
  		this.isExamationResult = false;
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../../assets/css/var.scss";
.enterExamationResult{
	position: fixed;
	top: 0;
	left: 0;
	padding: 15px;
	width: 100%;
	box-sizing: border-box;
	background: rgba(0,0,0,.6);

	.text{
		color: #fff;
		font-size: $font18;
		text-decoration: underline;
	}
	.close{
		position: absolute;
		right: 0;
		bottom: 0;
		width: 23px;
		height: 23px;
		line-height: 23px;
		background: rgba(0,0,0,.9);
		text-align: center;
		.iconfont{
			color: #fff;
		}
	}
}
</style>



