(function(){
    window.util = {
        scroll_To: function() {

        },
        // 局部滚动到指定位置
        partScrollToBottom: function() {
            // 滚动内容的总高度 - 滚动内容的可视区域
            let home_top = document.querySelector('.home-top');
            let loadmore_content = document.querySelector('.mint-loadmore-content');
            home_top.scrollTop = loadmore_content.clientHeight - home_top.clientHeight;
        },
        // 搜索是否有指定class
        hasClass: function(elements, cName) {
            // ( \\s|^ ) 判断前面是否有空格 （\\s | $ ）判断后面是否有空格 两个感叹号为转换为布尔值 以方便做判断 
            if (elements.className !== undefined) {
                return !!elements.className.match(new RegExp( "(\\s|^)" + cName + "(\\s|$)"));  
            }
        },
        // 添加指定class
        addClass: function(elements, cName) {
            if( !this.hasClass( elements,cName ) ){ 
                elements.className += " " + cName; 
            }; 
        },
        // 删除指定class
        removeClass: function(elements, cName) {
            if( this.hasClass( elements,cName )){ 
                elements.className = elements.className.replace( new RegExp( "(\\s|^)" + cName + "(\\s|$)" )," " ); // replace方法是替换 
            }; 
        }
    };
})();
export default util;


