<template>
	<div class="register">
		<div class="register_page" v-show="toggle">
			<div class="register_div">
				<i class="iconfont icon-nicheng"></i>
				<input type="text" placeholder="请输入昵称" v-model="nickname" />
			</div>
			<div class="register_div">
				<i class="iconfont icon-shouji"></i>
				<input type="text" placeholder="请输入手机号" v-model="phone" />
			</div>
			<div class="register_div">
				<i class="iconfont icon-mima"></i>
				<input type="password" placeholder="请输入密码" v-model="password" />
			</div>
			<div class="verification ">

				<input type="text" placeholder="请输入验证码" v-model="verification_code" />
				<img src="http://120.24.6.91:8082/tool/code" class="verification_img" />
			</div>
			<p class="nextone" @click="clicknext">下一步</p>
		</div>
		<div class="phone_code_page register_page" v-show="toggle2">
			<div class="verification ">
				<input type="text" placeholder="请输入手机号" v-model="phone" />
				<p>重新获取<i v-for="items in list">（{{ items.time }}）</i></p>
			</div>
			<div class="register_div">

				<input type="text" placeholder="请输入手机验证码" v-model="phone_verification"/>
			</div>
			<p class="nextone" @click="clickregister">注册</p>

		</div>
	</div>
</template>

<script>
	import Vue from "vue";
	import { Toast } from 'mint-ui';
	export default {
		name: 'register',
		data() {
			return {
				nickname: "",
				phone: "",
				password: "",
				verification_code: "",
				phone_num: "",
				verification_result: '',
				guid: "hhffkkfkkfksdfkkf1223566",
				toggle: true,
				toggle2: false,
				list: [{}],
				phone_verification:""
			}
		},
		methods: {
			clicknext() {
				if(this.nickname == "" || this.phone == "" || this.password == "" || this.verification_code == "") {
					Toast({
						message: '请输入内容',
						position: 'middle',
						duration: 1000
					});

				} else if(this.phone.length !== 11) {
					Toast({
						message: '手机号码不正确',
						position: 'middle',
						duration: 1000
					});
				} else if(this.password.length < 6) {
					Toast({
						message: '密码长度大于六位',
						position: 'middle',
						duration: 1000
					});
				}

				if(this.password.length >= 6 && this.phone.length == 11 && this.nickname !== "" && this.phone !== "" && this.password !== "" && this.verification_code !== "") {

					this.$http({
						url: 'http://120.24.6.91:8082/tool/sendmsgbind',
						method: 'POST',
						params: {
							validateCode: this.verification_code,
							        tel: this.phone
						}
					}).then(function(response) {

						console.log(response.body);
						this.verification_result = response.body;
						if(this.verification_result == 3) {
							Toast({
								message: '验证码输入有误',
								position: 'middle',
								duration: 1000
							});
						} else if(this.verification_result == 7) {
							Toast({
								message: '手机号码已经被注册过',
								position: 'middle',
								duration: 1000
							});

						} else if(this.verification_result == 1) {
							this.toggle = false;
							this.toggle2 = true;
							var count = 60;
				var _this = this;
				var times = setInterval(function() {
					for(var i = 0; i < _this.list.length; i++) {
						Vue.set(_this.list[i], 'time', count);
					}
					count--;
					console.log(count);
					if(count < 0) {
						clearInterval(times)
					}
				}, 1000)
						}

					}, function(response) {
						console.log(response);
					});
				}

			},
			clickregister() {
this.$http.get("http://120.24.6.91:8082/wx/powderSystem/regUser?tel=" + this.phone + "&pwd=" + this.password + "&nickname=" + this.nickname + "&guid="+this.guid+"&viriCode="+this.phone_verification 

).then((response) => {
								console.log(response.body + "注册结果");
								if(response.body == -2) {
									Toast({
										message: '用户已经存在，无需注册',
										position: 'middle',
										duration: 1000
									});
								}
								if(response.body == 0) {
									Toast({
										message: '注册失败',
										position: 'middle',
										duration: 1000
									});
								}
								if(response.body >0) {
									Toast({
										message: '注册成功',
										position: 'middle',
										duration: 1000
									});
									this.$router.push("login");
								}
								
							})
						} 
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/css/var.scss';
	.register {
		position: relative;
		top: 130px;
		height: calc(100vh - 130px);
		:-ms-input-placeholder {
			color: $pagebg;
			font-size: $font12;
		}
		div.register_div {
			height: 52px;
			line-height: 52px;
			width: 70%;
			margin: auto;
			background-color: $whitecolor;
			border: 1px solid $barbg;
			margin-bottom: 15px;
			border-radius: 2px;
			input {
				border: none;
				padding: 0;
				margin: 0;
				vertical-align: top;
				height: 50px;
				line-height: 50px;
				width: 70%;
				font-size: $font16;
				padding: 10px;
				box-sizing: border-box;
				/*	 background: #00f;*/
			}
			i {
				font-size: 25px;
				padding: 0 8px;
			}
		}
		div.verification {
			display: flex;
			flex-direction: row;
			height: 52px;
			line-height: 52px;
			width: 70%;
			margin-left: 15%;
			margin-bottom: 15px;
			position: relative;
			input {
				border-radius: 2px;
				background-color: $whitecolor;
				border: 1px solid $barbg;
				padding: 10px;
				box-sizing: border-box;
				margin-right: 10px;
				width: calc(100% - 100px);
			}
			.verification_img {
				width: 90px;
				height: 50px;
			}
		}
		.nextone {
			height: 52px;
			line-height: 52px;
			text-align: center;
			color: $bluecolor;
			background-color: $whitecolor;
			width: 62%;
			margin: auto;
			border-radius: 2px;
			font-size: $font16;
		}
		.phone_code_page {
			.verification p {
				display: inline-block;
				background-color: $whitecolor;
				text-align: center;
				color: $bluecolor;
				width: 110px;
    			flex: 0 0 110px;
			}
			i {
				font-style: normal;
			}
		}
	}
</style>


