<template>
  <div class="signIn" v-if="flag" @click="signIn">
    签到
  </div>
</template>

<script>
export default {
  name: 'signIn',
  props: ['flag'],
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    signIn() {
      this.$emit('emitSignIn', false)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../../assets/css/var.scss";
.signIn {
	position: absolute;
	bottom: 65px;
	left: 50%;
	padding: 7px 30px;
	transform: translateX(-50%);
	background: #fff;
	border: 1px solid $bluecolor;
	border-radius: 20px;
	color: $bluecolor;
}
.bottom70{
  bottom: -70px;
}
</style>



