<template>
	<div class="taskEdit">
		<div class="taskEdit_List">
			<!--箭头 表名 arrowdown -->
			<p @click="ulshow">{{activename}}
				<span :class="{'arrowdown':!toggle,'arrowup': toggle}"></span></p>
			<ul v-if="toggle">
				<li v-for="(item,index) in items" @click="selectStyle(item,index)"
					 :class="{'active':item.active,'unactive':!item.active}" >
					{{item.select}}
				</li>
			</ul>
		</div>
		<component :is="com"></component>
		
		<v-mask :flag="toggle"></v-mask>
	</div>
</template>

<script>
	import Vue from 'vue'
	
	import { Checklist } from 'mint-ui';
	// 引用遮罩层组件
	import mask from '../functionComponents/mask'
	// 引用普通公告组件
	import announcement from '../functionComponents/taskAnnouncement'
	// 引用视频直播组件
	import liveVideo from '../functionComponents/taskliveVideo'
	// 引用外部活动组件
	import active from '../functionComponents/taskActive'
	export default {
		name: 'taskEdit',
		data() {
			return {

				items: [　　{
					select: '普通公告',
					component: 'announcement',
					
				}, 　　　　　　　　　　 {
					select: '直播视频',
					component: 'liveVideo',
					color:"active"
				}, 　　　　　　　　　　 {
					select: '外部活动',
					component: 'active',
					
				}　　　　　　　　　　　　　　　　　],
				activename: "直播视频",
				toggle: false,
				com: 'liveVideo',

			}
		},
		methods: {
			ulshow: function() {
			
				this.toggle = !this.toggle;
				console.log(this.$refs.li_list);
			},

			selectStyle: function(item, index) {
				this.activename = this.items[index].select;
				this.toggle = !this.toggle;
				this.com = this.items[index].component;
				this.items.forEach(function (item) {
　　　　　　　　　　Vue.set(item,'active',false);
　　　　　　　　});
　　　　　　　　Vue.set(item,'active',true);
			}

		},
		components: {
			'v-mask': mask,
			announcement,
			liveVideo,
			active
		}
	}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/css/var.scss';
	.taskEdit_List {
		height: 45px;
		line-height: 45px;
		width: 100vw;
		text-align: center;
		background-color: $whitecolor;
		margin-bottom: 10px;
		position: relative;
		z-index: 10;
		p {
			border-bottom: 1px solid $bordercolor;
			position: relative;
			font-size: $font16;
			.arrowdown:after {
				content: "";
				border-left: 5px solid transparent;
				border-right: 5px solid transparent;
				border-top: 8px solid $txtcolor;
				position: absolute;
				top: 18px;
				margin-left: 5px;
			}
			.arrowup:after {
				content: "";
				border-left: 5px solid transparent;
				border-right: 5px solid transparent;
				border-bottom: 8px solid $txtcolor;
				position: absolute;
				top: 18px;
				margin-left: 5px;
			}
		}
		ul {
			background-color: $whitecolor;
		}
		li {
			height: 44px;
			line-height: 44px;
			border-bottom: 1px solid $pagebg;
			margin: 0 20px;
			font-size: $font14;
		}
		
		.taskEdit_List_active {
			color: $bluecolor;
		}
		.active {
			color: #1c91d4;
		}
		.unactive {
			color: #464646;
		}
	}
</style>


