         <!-- 发送消息实时 -->
         <div v-for="(item,index) in this.$store.state.practiceObj.selfSendPracticeList">
            <msgTime :time="item.Timer" v-if="item.isShowTime"></msgTime>
            <div class="chat-box" :class="{
                'self-signIn': item.ismy && item.dataType == 3,
                'others-signIn': !item.ismy && item.dataType == 3,
                'self-practice': item.ismy && item.dataType == 2, 
                'others-practice': !item.ismy && item.dataType == 2,
                'self': item.ismy && item.dataType == 1,
                'others': !item.ismy && item.dataType == 1
                }">
              <div v-if="item.ismy && item.dataType == 2">
                <span class="chat-msg">
                  <p class="username">{{item.nickname}}</p>
                  <div class="text">
                    <span class="text-practice" v-html="item.Title"></span>
                    <div class="text-radio">
                      <ul>
                        <li :class="{'on': item.submitStatus && option.IsTrue == 1 && item.showSuccessAnswer}" v-for="(option,optionsIndex) in item.options" @click.stop="!item.submitStatus && practiceOptionsClick($event,optionsIndex)">
                        <span>{{option.Taxis}}</span>
                        <b v-html="option.Option"></b>
                        </li>
                      </ul>
                    </div>
                    <div class="text-success" v-if="item.submitStatus && item.showSuccessAnswer">
                      <div class="text-answer">正确答案：
                      <span v-if="answer.IsTrue == 1" v-for="answer in item.options">{{answer.Taxis}}</span>
                      </div>
                      <div class="text-detail">
                        <b>解析</b>{{item.Explain}}
                      </div>
                      <div class="text-flex">
                        <span class="text-flex-span">
                          <p>答题人数</p>
                          <h3>{{item.People}}</h3>
                        </span>
                        <span class="text-flex-span">
                          <p>全网正确率</p>
                          <h3>{{item.Accuracy | tt}}%</h3>
                        </span>
                        <span class="text-flex-span">
                          <p>人关注</p>
                          <h3>{{item.CollectionAll}}</h3>
                        </span>
                        <span class="text-flex-span">
                          <p>笔记</p>
                          <h3>{{item.NotesCount}}</h3>
                        </span>
                      </div>
                      <div class="text-flex" v-if="false">
                        <span class="text-flex-border">A</span>
                        <span class="text-flex-border">B</span>
                        <span class="text-flex-border">C</span>
                        <span class="text-flex-border">D</span>
                        <span class="text-flex-border">E</span>
                      </div>
                    </div>
                    <div v-if="!item.submitStatus" class="text-btn border-top-1px" @click="!item.submitStatus && practiceOptionsSubmit($event,index)">
                        提交
                    </div>
                  </div>
                  <div class="participate" v-if="item.submitStatus">
                    <i class="iconfont icon-iconfontweibiaoti1"></i>
                    参与人数 <span>{{item.participate}}</span>
                  </div>
                </span>
                <span class="chat-face">
                  <img :src="item.headimgurl" alt="">
                </span>
              </div>
              <div v-else-if="!item.ismy && item.dataType == 2">
                <span class="chat-face">
                  <img :src="item.headimgurl" alt="">
                </span>
                <span class="chat-msg">
                  <p class="username">{{item.nickname}}</p>
                  <div class="text">
                    <span class="text-practice" v-html="item.Title"></span>
                    <div class="text-radio">
                      <ul>
                        <li :class="{'on': item.submitStatus && option.IsTrue == 1  && item.showSuccessAnswer}" v-for="(option,optionsIndex) in item.options" @click.stop="!item.submitStatus && practiceOptionsClick($event,optionsIndex)">
                        <span>{{option.Taxis}}</span>
                        <b v-html="option.Option"></b>
                        </li>
                      </ul>
                    </div>
                    <div class="text-success" v-if="item.submitStatus && item.showSuccessAnswer">
                      <div class="text-answer">正确答案：
                      <span v-if="answer.IsTrue == 1" v-for="answer in  item.options">{{answer.Taxis}}</span>
                      <span v-if="item.isSelfAnswer" :class="{'off': !item.isSuccess}">　你的答案是：{{item.selfAnswer}}</span>
                      </div>
                      <div class="text-detail">
                        <b>解析</b>{{item.Explain}}
                      </div>
                      <div class="text-flex">
                        <span class="text-flex-span">
                          <p>答题人数</p>
                          <h3>{{item.People}}</h3>
                        </span>
                        <span class="text-flex-span">
                          <p>全网正确率</p>
                          <h3>{{item.Accuracy | tt}}%</h3>
                        </span>
                        <span class="text-flex-span">
                          <p>人关注</p>
                          <h3>{{item.CollectionAll}}</h3>
                        </span>
                        <span class="text-flex-span">
                          <p>笔记</p>
                          <h3>{{item.NotesCount}}</h3>
                        </span>
                      </div>
                      <div class="text-flex" v-if="false">
                        <span class="text-flex-border">A</span>
                        <span class="text-flex-border">B</span>
                        <span class="text-flex-border">C</span>
                        <span class="text-flex-border">D</span>
                        <span class="text-flex-border">E</span>
                      </div>
                    </div>
                    <div v-if="!item.submitStatus" class="text-btn border-top-1px" @click="!item.submitStatus && practiceOptionsSubmit($event,index)">
                        提交
                    </div>
                  </div>
                  <div class="participate" v-if="item.submitStatus">
                    <i class="iconfont icon-iconfontweibiaoti1"></i>
                    参与人数 <span>{{item.participate}}</span>
                  </div>
                </span>
              </div>
              <div v-else-if="item.ismy && item.dataType == 1">
                <span class="chat-msg">
                  <p class="username">{{item.nickname}}</p>
                  <div class="text" :data-uid="item.Uid" :data-index="index" :data-contentId="item.Id" @touchstart="msgTouchstart" @touchmove="msgTouchmove"  @touchend="msgTouchend">
                    {{item.MsgContent}}
                  </div>
                </span>
                <span class="chat-face">
                  <img :src="item.headimgurl" alt="">
                </span>
              </div>
              <div v-else-if="!item.ismy && item.dataType == 1">
                <span class="chat-face">
                  <img :src="item.headimgurl" alt="">
                </span>
                <span class="chat-msg">
                  <p class="username">{{item.nickname}}</p>
                  <div class="text" :data-uid="item.Uid" :data-index="index" :data-contentId="item.Id" @touchstart="msgTouchstart" @touchmove="msgTouchmove"  @touchend="msgTouchend">
                    {{item.MsgContent}}
                  </div>
                </span>
              </div>
              <div v-else-if="item.ismy && item.dataType == 3">
                <span class="chat-msg">
                  <p class="username">{{item.nickname}}</p>
                  <div class="text">
                    第<span class="fontBlue fontBold">{{item.count}}</span>位签到
                  </div>
                </span>
                <span class="chat-face">
                  <img :src="item.headimgurl" alt="">
                </span>
              </div>
              <div v-else-if="!item.ismy && item.dataType == 3">
                <span class="chat-face">
                  <img :src="item.headimgurl" alt="">
                </span>
                <span class="chat-msg">
                  <p class="username">{{item.nickname}}</p>
                  <div class="text">
                    第<span class="fontBlue fontBold">{{item.count}}</span>位签到
                  </div>
                </span>
              </div>
          </div>
        </div>
  
    	</mt-loadmore>

     
  
      <!-- 消息发送 -->
    	<div class="home-bottom">
         <!-- 签到 -->
        <signIn :flag="signInArr.show" :data-id="signInArr.id" @emitSignIn="_emitSignIn"></signIn>
    		<!-- <span class="voice"><i class="iconfont icon-yuyin"></i></span> -->
        <span class="input" v-if="$store.state.practiceObj.groupBanned || $store.state.practiceObj.selfBanned">
          <input ref="chatInput" v-model="bannedText" disabled type="text">
        </span>
        <span class="input" v-else>
          <input ref="chatInput" v-model.trim="chatInput" @click.stop="getInputClick" @focus.stop="getInputFocus" @blur="getInputBlur" type="text">
        </span>
        <span v-if="!isSend">
          <!-- <span class="expression" @click="openExpression"><i class="iconfont icon-biaoqing"></i></span> -->
          <span class="add" @click="openMoreFunction"><i class="iconfont icon-tianjia"></i></span>
        </span>
        <a v-if="isSend" class="send" href="javascript:;" @click="chatInputSend">发送</a>
    	</div>

      <div class="home-more border-top-1px">
        
        <!-- 表情 -->
        <expression :flag="isExpression"></expression>        

        <!-- 更多功能  -->
        <moreFunction :flag="isMoreFunction"></moreFunction>
      </div>
    </div>

    <!-- 选择练习题题目 -->
    <selectPracticeTitle></selectPracticeTitle>

    <div class="longTap" ref="longTap">
      <span ref="copy" @touchstart="copy">复制</span>
      <span ref="banned" @touchstart="banned">禁言</span>
      <span ref="withdraw" @touchstart="withdraw">撤回</span>
    </div>


    <!-- 进入每日一练结果 -->
    <enterExaminationResult></enterExaminationResult>

    

  </div>
</template>

<script>

// 工具函数
import util from '@/assets/js/util'
// 表情
import expression from '../functionComponents/expression'
// 选择练习题题目组件
import selectPracticeTitle from '../functionComponents/selectPracticeTitle'
// 签到组件
import signIn from '../functionComponents/signIn'
// 进入每日一题结果组件
import enterExaminationResult from '../functionComponents/enterExaminationResult'
// 上拉加载组件、消息提示框组件
import { Loadmore,Toast,Indicator } from 'mint-ui'
// 消息时间组件
import msgTime from '../functionComponents/msgTime'
// 消息撤回组件
import msgWithdraw from '../functionComponents/msgWithdraw'
// 更多功能组件
import moreFunction  from '../functionComponents/moreFunction'

export default {
  name: 'home',
  data () {
    return {
      // 发送按钮
      isSend: false,
      // 展开更多功能
      isMoreFunction: false,
      // 表情
      isExpression: false,
      // 发送的文本消息
      chatInput: '',
      bannedText: '已被禁言',
      // 每次保存第一条历史消息id
      historyMsgId: false,
      // 长按
      timeOutEvent: 0,
      // 签到
      signInArr: {}
    }
  },
  // 进入该路由时调用
  beforeRouteEnter (to, from, next) {
    next(vm => {
      // 滚动到底部
      util.partScrollToBottom();
    });
  },
  mounted () {
      // 初始化
      this._init();

      let doc = document.body || document.documentElement;
      // 全局回车事件
      doc.addEventListener('keydown', (event) => {
        if (event.keyCode === 13) {
          if (this.chatInput) {
            // 发送
            this._send();
          }
        }
      }, false);

      // 聊天区域点击事件
      this.$refs.loadmore.$el.addEventListener('touchstart', () => {
        this.$refs.chatInput.blur();
        this.hideWithDraw();
      }, false);

    // 滚动触发时，隐藏长按显示的信息
    let self = this;
    this.$refs.loadmore.$el.addEventListener('scroll', function() {
      self.hideWithDraw();
    }, false);

  },
  methods: {
  	// 上拉加载
  	loadTop() {
      Indicator.open();      
      this.loadHistoryData(this.historyMsgId, () => {
        // 关闭加载动画
        this.$refs.loadmore.onTopLoaded();
        Indicator.close();
      });
  		
  	},
    // 隐藏长按显示的信息
    hideWithDraw() {
      this.$refs.longTap.style.display = 'none';
      this.$refs.longTap.removeAttribute('data-contentId');
      for (let i in this.$refs.longTap.children) {
        let v = parseInt(i, 10);
        if (isNaN(v)) return;
        this.$refs.longTap.children[i].style.display = 'block';
      }
    },
    // 复制
    copy(event) {
      console.log('复制');
      console.log(event);
      this.hideWithDraw();
    },
    // 禁言
    banned(event) {
      console.log('禁言');
      console.log(event); 
      let uid = event.target.parentNode.getAttribute('data-uid');
      
      this._ajax(`${this.$store.state.url}/wx/powderSystem/edUserInfoProhibitState?uid=${uid}&openId=${userObj.openId}&groupId=${userObj.groupId}&isProhibit=1`, (res) => {
        console.log(res);
        // if (res.body == 1) {
          let obj = {
            Uid: uid,
            banned: false,
            dataType: 4
          };
          MessageSend(JSON.stringify(obj));
          this.hideWithDraw();
        // }
      });
    },
    // 撤回
    withdraw(event) {
      console.log('撤回');
      console.log(event);
      this.hideWithDraw();
    }, 
    // 获取子组件签到返回来的事件
    _emitSignIn(val) {

      this._ajax(`${this.$store.state.url}/wx/powderSystem/addUserSignLog?signId=${this.signInArr.id}&uid=${userObj.uid}&openId=${userObj.openId}`, (res) => {
        if (res.status === 200) {
          let list = this.$store.state.practiceObj.selfSendPracticeList;
          let time = new Date();
          this.signInArr.show = false;
          let obj = {
            Uid: userObj.uid,
            nickname: "刘小龙",
            headimgurl: '',
            Id: Math.floor(Math.random() * 1000 + 1),
            dataType: 3,
            show: this.$store.state.practiceObj.signIn,
            count: res.body,
            ismy: true,
            Timer: time.getTime(),
          };

          list.push(obj);
          // 发送websocket
          MessageSend(JSON.stringify(obj));
          this.$refs.loadmore.$el.style.paddingBottom = '60px';
          // 滚动到底部
          this.$nextTick(() => {
            util.partScrollToBottom();
          });
          
        }
      });
    },
    // 消息touchstart事件
    msgTouchstart(event) {      
      console.log(event);
      this.timeOutEvent = setTimeout(() => {
        this.timeOutEvent = 0;
        // 获取当前消息id
        let contentId = event.target.getAttribute('data-contentId');
        let uid = event.target.getAttribute('data-uid');
        let longTap = this.$refs.longTap;
        // 要先显示，否则后面获取不到宽度
        longTap.style.display = '-webkit-box';
        longTap.style.display = 'flex';
        let longTap_width = parseInt(longTap.clientWidth, 10);
        let longTap_height = parseInt(longTap.clientHeight, 10);
        let document_width = parseInt(document.body.clientWidth, 10);
        let document_height = parseInt(document.body.clientHeight, 10);
        let y = event.changedTouches[0].clientY - (longTap_height + 20);
        let x = event.changedTouches[0].clientX - (longTap_width / 2);

        // 如果长按的信息是自己发送的，就隐藏禁言
        if (uid == userObj.uid) {
           this.$refs.banned.style.display = 'none';
        }
        // 如果长按的信息不是自己发送的，就隐藏撤回，并且如果不是管理员的情况下，就隐藏禁言
        if (uid != userObj.uid || this.$store.state.uid == 1) {
          this.$refs.withdraw.style.display = 'none';
          this.$refs.banned.style.display = 'none';
        }
        // 如果长按的信息不是自己发送的，并且如果是管理员的情况下，就显示禁言
         if (uid != userObj.uid || this.$store.state.uid == 1) {
          this.$refs.banned.style.display = 'block';
         }

        longTap.setAttribute('data-uid', uid);
        longTap.setAttribute('data-contentId', contentId);

        // 防止出界
        if (x < 0) {
          x = 0;
        } else if (x >= document_width - longTap_width) {
          x = document_width - longTap_width - 2;
        }
        if (y < 0) {
          y = 0;
        } else if (y >= document_height - longTap_height) {
          y = document_height - longTap_height - 2;
        }
        
        longTap.style.left = x + 'px';
        longTap.style.top = y + 'px';
        console.log('长按');
      }, 500);
    },
    // 消息touchmove事件
    msgTouchmove(event) {
      clearTimeout(this.timeOutEvent);
      this.timeOutEvent = 0;
      console.log('移动');
    },
    // 消息touchend事件
    msgTouchend(event) {
      clearTimeout(this.timeOutEvent);
      this.timeOutEvent = 0;
      console.log('离开');
    },
    // 输入框点击
    getInputClick(event) {
      let input = event.target;
      let doc = document.body || document.documentElement;
       setTimeout(() => {
          //  解决ios键盘弹起被挡住的问题
          doc.scrollTop = doc.clientHeight;
          // target.scrollIntoViewIfNeeded();
          // 隐藏长按显示的信息
          this.hideWithDraw();
      }, 100)
    },
    // 获取焦点
    getInputFocus() {    
      this.isSend = true;
      // 隐藏更多功能和表情
      this.isMoreFunction = false;
      this.isExpression = false;     
    },
    // 失去焦点 
    getInputBlur() {
      setTimeout(() => {
           this.isSend = false;
      },100);
    },
    // 练习题选项单选
    practiceOptionsClick(event, index) {
      console.log(event);
      // 当前元素所有的同级元素删除 'on' 样式
      let li = event.target.parentNode.parentNode.children;
      console.log(li);
      for(let i in li) {
         util.removeClass(li[i],'on');
      }
      // 当前选中的元素添加 'on' 样式
      util.addClass(li[index],'on')
    },
    // 练习题选项提交
    practiceOptionsSubmit(event,index) {
      let list = this.$store.state.practiceObj.selfSendPracticeList[index];
      let textRadio = event.target.parentNode.children;
      let li = '';
      let optionsIndex = false;
      for (let i in textRadio) {
        if(util.hasClass(textRadio[i],'text-radio')) {
          li = textRadio[i].children[0].children;
        }
      }
      for (let i in li) {
        if (util.hasClass(li[i],'on')) {
          optionsIndex = i;
        }
      }
      if (!optionsIndex) {
        Toast('请选择你的答案');
        return false;
      }
     
      // 判断正确答案
      if (list.options[optionsIndex].IsTrue == 1) {
        // 保存用户答题记录
        this._ajax(`${this.$store.state.url}/wx/powderSystem/addDayPractiseQuestionLog?qid=${list.Id}&optionId=${list.options[optionsIndex].Id}&isTrue=0&uid=${userObj.uid}&openId=${userObj.openId}&addDayPractiseQuestionLog=${list.PractiseItem}`, (res) => {
          // 保存参与人数
          this.$set(list, 'participate', res.body);
          console.log('正确');
        });
      } else {
        // 保存用户答题记录
        this._ajax(`${this.$store.state.url}/wx/powderSystem/addDayPractiseQuestionLog?qid=${list.Id}&optionId=${list.options[optionsIndex].Id}&isTrue=1&uid=${userObj.uid}&openId=${userObj.openId}&addDayPractiseQuestionLog=${list.PractiseItem}`, (res) => {
           // 保存参与人数
          this.$set(list, 'participate', res.body);
          console.log('错误');
        });
        // util.addClass(li[optionsIndex],'off');
      }
       
      // 提交按钮状态
      this.$set(list, 'submitStatus', true);
      console.log(list);
      let answer = [];
      if (localStorage.getItem('selectedAnswer')) {
        answer = JSON.parse(localStorage.getItem('selectedAnswer'));
        for(let i in answer) {
          // 判断是否做过相同的题
          if (answer[i].Id == list.Id) {
            answer.splice(i, 1);
          }
        }
      } 
      answer.push({
        Id: list.Id,
        optionsIndex
      });
       // 保存当前选中的题和答案到本地缓存
      localStorage.setItem('selectedAnswer',JSON.stringify(answer));
    },
    // 打开表情
    openExpression() {
      this.isMoreFunction = false;
      this.isExpression = !this.isExpression;
      // 滚动到底部
      this.$nextTick(() => {
        util.partScrollToBottom();
      });
    },
    // 打开更多功能
    openMoreFunction() {
      this.isExpression = false;
      this.isMoreFunction = !this.isMoreFunction;
      // 滚动到底部
      this.$nextTick(() => {
        util.partScrollToBottom();
      });
    },
    // 按钮发送
    chatInputSend() {
      if (this.chatInput) {
        this._send();
      }
    },
    loadHistoryData(id='', cb) {
      // 默认加载历史消息
      this._ajax(`${this.$store.state.url}/wx/powderSystem/getUserMessage?id=${id}`, (res) => {
        let list = this.$store.state.practiceObj.selfSendPracticeList;
        console.log(list);
        for(let i in res.body) {
          if (res.body[i].Uid == userObj.uid) {
            res.body[i].ismy = true;
          } else {
            res.body[i].ismy = false;
          }
          res.body[i].dataType = 1;
          if (id != '') {
            list.unshift(res.body[i]);
          } else {
            list.push(res.body[i]);
          }
          
        }
        // 保存第一条历史消息id
        this.historyMsgId = res.body[0].Id;
        cb && cb();
      });
    },
    // 初始化
    _init(){
      
      // 加载历史消息
      this.loadHistoryData();

      setInterval(() => {
        let val = document.getElementById('msg').value;
        try {
          let s = JSON.parse(val);
          if(s.Uid == userObj.uid){
            s.ismy=true;
          }else{
            s.ismy=false;
          }
          
          if (s.Uid) {
              // 判断是否接收到的是练习题
              // dataType：1 为文本消息  2  为练习题   3为签到   33为发起签到   4为用户禁言   44为全体禁言  
              let list = this.$store.state.practiceObj.selfSendPracticeList;
              if (s.dataType == 1) {
                if (list.length <= 0) {
                  list.push(s);
                  // 每次获取到新消息就自动滚动到底部
                  this.$nextTick(() => {
                    util.partScrollToBottom();
                  });
                } else {
                    if (list[list.length-1].Id != s.Id && !s.ismy) {
                        list.push(s);
                        // 每次获取到新消息就自动滚动到底部
                        this.$nextTick(() => {
                          util.partScrollToBottom();
                        });
                    }
                }
              } else if(s.dataType == 2) {
                  if(localStorage.getItem('selectedAnswer')) {
                    console.log(11);
                    if (!s.ismy && s.showSuccessAnswer) {
                       console.log(22);
                      // 从本地读缓存，看下是否之前答过该题目
                      let data = JSON.parse(localStorage.getItem('selectedAnswer'));
                      console.log(data);
                      console.log(s);
                      for(let i in data) {
                        // 判断当前接收的试题是否等于，我之前答过的题
                        console.log('i = ' + i);
                        if (s.Id == data[i].Id) {
                          if (s.options[data[i].optionsIndex].isTrue == 1) {
                            s.isSuccess = true;
                          } else {
                            s.isSuccess = false;
                          }
                          s.isSelfAnswer = true;
                          s.selfAnswer = data[i].optionsIndex;
                        }
                      }
                    }
                  }
                   console.log('练习题');
                  console.log(s);
                  if (list.length <= 0) {
                    list.push(s);
                  } else {
                    console.log('卧槽进来没有？');
                    if (list[list.length-1].Id != s.Id && !s.ismy) {
                      list.push(s);
                      // 每次获取到新消息就自动滚动到底部
                      this.$nextTick(() => {
                        util.partScrollToBottom();
                      });
                    }
                  }
              } else if(s.dataType == 33) {
                  this.signInArr = s;
              } else if(s.dataType == 3) {
                 if (list.length <= 0) {
                    list.push(s);
                  } else {
                    if (list[list.length-1].Id != s.Id && !s.ismy) {
                      list.push(s);
                      // 每次获取到新消息就自动滚动到底部
                      this.$nextTick(() => {
                        util.partScrollToBottom();
                      });
                    }
                  }
              } else if(s.dataType == 44) {
                this.$store.commit('setGroupBanned', s.banned);
              } else if(s.dataType == 4) {
                // 如果当前禁言的uid等于自己的uid
                let selfBanned = '';
                if(s.Uid == userObj.uid) {
                  selfBanned = true;
                } else {
                  selfBanned = false;
                }
                this.$store.commit('setSelfBanned', selfBanned);
              }
              
            
          }
        } catch(err) {
          // console.log('错误了');
          // console.log(err);
        }
     },200);

     // 滚动到底部
     setTimeout(() => {
      util.partScrollToBottom();
     },500);
    

    },
    // 提交
    _submit(type, content, contentId) {
      let time = new Date();
      let obj = {
          Uid: userObj.uid,
          nickname: "刘小龙",
          headimgurl: '',
          MsgType: type,
          MsgContent: content,
          Id: contentId,
          Timer: time.getTime(),
          ismy: true,
          dataType: 1
        };
        console.log('发送')
        console.log(obj);
        this.$store.state.practiceObj.selfSendPracticeList.push(obj);

        if (localStorage.getItem('timer')) {
          let timer = localStorage.getItem('timer');
          let s = 5 * 60 * 1000;
          if (time - timer > s) {
            obj.isShowTime = true;
          } else {
            obj.isShowTime = false;
          }
        } else {
          // 保存当前时间的时间戳
          localStorage.setItem('timer', time.getTime());
          obj.isShowTime = true;
        }
        


        // 发送websocket
        MessageSend(JSON.stringify(obj));
        // 等待DOM已经被插入进入的时候，再滚动到底部
        this.$nextTick(() => {
          util.partScrollToBottom();
        }); 
    },
    // ajax 请求
    _ajax(url, cb) {
      this.$http.get(url).then( res => {
        if (res.status === 200){
            cb && cb(res);
        }
      }, error => {
       console.log(error);
     });
    },
    // 发送
    _send() {
      // 判断消息类型是否是字符串
      let MsgType = typeof this.chatInput == 'string' ? 1 : 0;
      
      // 获取消息id
      let msgId = null;
      let time = new Date().getTime();
      this._ajax(`${this.$store.state.url}/wx/powderSystem/addChatLog?uid=${userObj.uid}&openId=${userObj.openId}&msgType=${MsgType}&msgContent=${this.chatInput}&groupId=${userObj.groupId}&timer=${time}`, (res) => {
        if (res.status === 200) {
            msgId = res.body;
            this._submit(MsgType, this.chatInput, msgId);
            this.chatInput = '';
            this.isSend = false;
        }
      });
    }
  },
  // 注册组件
  components: {
  	mtLoadmore: Loadmore,
    msgTime,
    msgWithdraw,
    moreFunction,
    enterExaminationResult,
    signIn,
    selectPracticeTitle,
    expression
  }
}
</script>

