<script>

// 练习题组件
import selectPracticeTitle from '../functionComponents/selectPracticeTitle'

// 轮播组件
import { Swipe,SwipeItem } from 'mint-ui'
export default {
  name: 'moreFunction',
  props: ['flag'],
  data () {
    return {
      emitIsPractice: false
    }
  },
  methods: {
  	// 打开练习题题目选择
  	openPractice() {
  		this.emitIsPractice = !this.emitIsPractice;
  	},
    onParcticeChange(val) {
      this.emitIsPractice = val;// 4.外层调用组件方注册变更方法，将组件内的数据变更，同步到组件外的数据状态中
    },
    // 打开签到
    openSignIn() {
      console.log('签到');
      this.$http.get(`${this.$store.state.url}/wx/powderSystem/addUserSign?groupId=${userObj.groupId}&adminId=${userObj.adminId}`).then( res => {
        if (res.status === 200) {
          let obj = {
            Uid: userObj.uid,
            dataType: 33,
            show: true,     
            id: res.body
          };
          // 发送websocket
          MessageSend(JSON.stringify(obj));
        }
        
      }, error => {
        console.log(error);
      });
    }
  },
  components: {
  	mtSwipe: Swipe,
    mtSwipeItem: SwipeItem,
    selectPracticeTitle
  }
}
</script>

