<template>
  <div class="examinationResult">
   <p>今日参考人数：<span>{{nowDayData}}</span>
   	<router-link :to="'/errorPractice'" class="posright">查看易错题<i class="iconfont icon-shuangjiantou-you"></i></router-link>
   </p>
   <div class="examinationResult_title">
   	<span>昵称</span>
   	<span>正确题数</span>
   	<span>错误题数</span>
   <span @click="getsort">排名
   	<i class='iconfont ' :class="{'icon-jiantou-xia':!isActive,'icon-jiantou-shang': isActive}"></i></span>
   </div>
   <ul class="datalist">
				<li  v-for="(items,index) in datalist">
					<span>{{items.NickName}}</span>
					<span>{{items.OkCount}}</span>
					<span>{{items.NoCount}}</span>
					<span>{{items.Row}}</span>
				</li>
			</ul>
			<router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'examinationResult',
  data () {
    return {
     datalist: [],
     nowDayData:"",
     isActive:false,
     
    }
  },
  methods: {
  	getsort:function(){
  		this.datalist=this.datalist.reverse();
  		this.isActive=!this.isActive;
  	}
  },
  // 实例挂载完毕的回调函数
		mounted() {
			this.$http.get("http://120.24.6.91:8082/wx/powderSystem/getDayPractiseDataLog").then((response) =>{
				response=response.body;
				this.datalist=response.logList;
				this.nowDayData=response.nowDayData;
				console.log(this.datalist)
			});
		}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/css/var.scss';
.examinationResult{
	padding-top:90px;
	p{
		position:fixed;
		width:100%;
		height:50px;
		line-height: 50px;
		padding: 0 18px;
		background-color: #fff;
			box-sizing: border-box;
			top:0;
		
		span{ font-size:$font16;color:$bluecolor;}
		.posright{
			position: absolute;
			right: 15px;

		}
		i{
			margin-left: 8px;
			
		}
	}
	.examinationResult_title{
		position: fixed;
		display: -webkit-flex;
		display: flex;
		top: 50px;
		width: 100%;
		max-width:750px;
		text-align: center;
		height: 40px;
		line-height: 40px;
		color: $whitecolor;
		background-color: $txtcolor;
		span{
			flex: 1;
		}
		i{
			font-size: $font14;
			margin-left: 5px;
		}
	}
	.datalist {
	
		li {
			height: 40px;
			display: inline-block;
			display: flex;
			line-height: 40px;
			text-align: center;
			color:$txtcolor;
			background-color: #e5e5e5;
		}
		li:nth-of-type(odd){
			background-color:#f1f1f1 ;
		}
		span {
			flex: 1;
			text-align: center;
		}
	
	}
}

</style>



