    <p>长按发送给朋友</p>
    <p>邀请好友一起组队学习</p>
    <div class="pos_relative">
    	<img :src="this.info.ShareImg" />
    
    </div>
  </div>
</template>

<script>
export default {
  name: 'inviteFriends',
  data () {
    return {
     info:{}
    }
  },
  // 实例挂载完毕的回调函数
		mounted() {
			this.$http.get("http://120.24.6.91:8082//wx//powderSystem/getUserJoinImage?uid=" +536302).then((response) =>{
				response=response.body;
				this.info=response;
				console.log(this.info.ShareImg);
			});
		}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/css/var.scss';
.inviteFriends{
	height: 100vh;
	overflow: hidden;
	position:relative;
	padding-top:10%;
	.delicon{
		font-size:45px;
		position:absolute;
		right:15px;
		top:0px;
	}
	p{
		color: $bluecolor;
		text-align: center;
		height: 22px;
		line-height: 22px;
	}
	img{
		width:80%;
