<template>
	<div class="statement">

		<div class="displayflex">
			<span v-for="item in tablelist">{{item}}</span>
		</div>
	
			<ul class="datalist">
				
				<li  v-for="(items,index) in datalist">
					<span>{{items.Date}}</span>
					<span>{{items.SignCount}}</span>
					<span>{{items.MakeCount}}</span>
					<span>{{items.JoinCount}}</span>
				</li>
				
			</ul>
		
	</div>
</template>

<script>
	export default {
		name: 'statement',
		data() {
			return {
				tablelist: [
					"日期", "签到人数", "每日一练人数", "进群人数"
				],
				datalist: [
				]
			}
		},
		// 实例挂载完毕的回调函数
		mounted() {
			this.$http.get("http://120.24.6.91:8082/wx/powderSystem/getAllDayDataLog").then((response) =>{
				response=response.body;
				this.datalist=response;
			});
		}
		
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/css/var.scss';
	.statement {
		padding-top:40px;
		
		.displayflex {
			position:fixed;
			top:0;
			width:100%;
			max-width:750px;
			display: flex;
			background-color: $txtcolor;
			color: $whitecolor;
			height: 40px;
			line-height: 40px;
			font-size: $font14;
		}
		span {
			flex: 1;
			text-align: center;
		}
	}
	
	.datalist {
	
		li {
			height: 40px;
			display: inline-block;
			display: flex;
			line-height: 40px;
			text-align: center;
			color:$txtcolor;
			background-color: #e5e5e5;
		}
		li:nth-of-type(odd){
			background-color:#f1f1f1 ;
		}
	
	}
</style>


