import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		// 使用vuex保存当前用户身份 1 为普通用户   2 为 管理员
		uid: 2,
		// 聊天对象
		practiceObj:{
			// 消息数组对象
			selfSendPracticeList:[
				// {
				// 	userid: 179, 
				// 	username: "刘小龙", 
				// 	useravator: "./face.png", 
				// 	type: 1, 
				// 	content: "到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点到一点", 
				// 	contentId: 941,
				// 	timer: 1495790331046, 
				// 	ismy: true, 
				// 	dataType: 1
				// }
			],
			// 签到
			signIn: false,
			// 全局禁言
			groupBanned: false,
			// 当前自己是否禁言
			selfBanned: false
		},
		// 保存当前请求地址
		url: 'http://120.24.6.91:8082'
	},
	mutations: {
		setUid (state, val) {
			state.uid = val;
		},
		setGroupBanned (state, val) {
			state.practiceObj.groupBanned = val;
		},
		setSelfBanned (state, val) {
			state.practiceObj.selfBanned = val;
		}
	}
}) 




