<template>
  <div class="announcement">
   <div class="notice">

			<span>标题：</span>
   <textarea v-model="titletxt"></textarea>
			
   </div>
		
	<div class="choosecheck">
			<input type="checkbox" id="service" value="service" v-model.trim="checkedservice">
			<label for="service">服务号</label>
			<input type="checkbox" id="group" value="group" v-model.trim="checkedgroup">
			<label for="group">群系统</label>

		</div>
		<div class="sendButton" @click="send">确认发送</div>
  </div>
</template>

<script>
	import Vue from "vue"
	import { Toast } from 'mint-ui';
export default {
  name: 'announcement',
  data () {
    return {
      titletxt:"",
      checkedservice: true,
				checkedservice_num: "",
				checkedgroup: true,
				checkedgroup_num: ""
    }
  },
  methods: {
			send: function() {
				if( this.titletxt == "") {
					Toast({
						message: '请输入内容',
						position: 'middle',
						duration: 1000
					});
				}else {
					this.checkedgroup_num = this.checkedgroup ? 1 : 0;
					this.checkedservice_num = this.checkedservice ? 1 : 0;

					console.log(this.checkedservice_num);
					console.log(this.checkedgroup_num);
					this.$http.get("http://120.24.6.91:8082/wx/powderSystem/addNoticeInfo?title=" + this.titletxt + "&url=" + ""+ "&type=" + 1 + "&groupId=" + userObj.groupId + "&adminId=" + userObj.adminId + "&sendClient=" + this.checkedgroup_num + "&sendServer=" + this.checkedservice_num).then((response) => {
						console.log(response.body)
					})
				}

			}
		}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/css/var.scss';
.notice {
	height: 120px;
	border: 1px solid $bordercolor;
	background-color: $whitecolor;
	padding: 20px;
	box-sizing: border-box;
	span{
		vertical-align: top;
	}
	textarea{
		width: 80%;
    height: 69px;
    vertical-align: top;
    border: none;
    resize: none;
    line-height: 20px;
	}
}
.sendButton {
		height: 50px;
		line-height: 50px;
		color: #fff;
		text-align: center;
		font-size: $font18;
		background-color: $bluecolor;
		width: 90%;
		margin: auto;
		margin-top: 20px;
		border-radius: 3px;
	}
	.choosecheck {
		height: 40px;
		line-height: 40px;
		margin-left: 5%;
	}
</style>



