<template>
  <div class="msgTime">
    <span>{{time | getDateDiff}}</span>
  </div>
</template>

<script>
export default {
  name: 'msgTime',
  props: ['time'],
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import "../../assets/css/var.scss";
.msgTime{
  margin-top: 10px;
  text-align: center;
  span{
    padding: 3px 5px;
    display: inline-block;
    color: $whitecolor;
    background: $bordercolor;
    border-radius: 8px;
    font-size: 12px;
  }
}
</style>



