<template>
	<div class="liveVideo">
		<div class="onlive">
			<label for="title">标题：
   	 	<input type="text" id="title" class="title" v-model.trim="titletxt"/>
   	 </label>
			<label for="link">链接：
   	 	<input type="text" id="link" v-model.trim="linktxt"/>
   	 </label>
		</div>
		<div class="choosecheck">
			<input type="checkbox" id="service" value="service" v-model="checkedservice">
			<label for="service">服务号</label>
			<input type="checkbox" id="group" value="group" v-model="checkedgroup">
			<label for="group">群系统</label>

		</div>
		<div class="sendButton" @click="send">确认发送</div>
	</div>
</template>

<script>
	import Vue from "vue";
	import { Toast } from 'mint-ui';
	export default {
		name: 'liveVideo',
		data() {
			return {
				linktxt: "",
				titletxt: "",
				checkedservice: true,
				checkedservice_num: "",
				checkedgroup: true,
				checkedgroup_num: ""
			}
		},

		methods: {
			send: function() {
				if(this.linktxt == "" || this.titletxt == "") {
					Toast({
						message: '请输入内容',
						position: 'middle',
						duration: 1000
					});
				} else {
					this.checkedgroup_num = this.checkedgroup ? 1 : 0;
					this.checkedservice_num = this.checkedservice ? 1 : 0;

					console.log(this.checkedservice_num);
					console.log(this.checkedgroup_num);
					this.$http.get("http://120.24.6.91:8082/wx/powderSystem/addNoticeInfo?title=" + this.titletxt + "&url=" + this.linktxt + "&type=" + 2 + "&groupId=" + userObj.groupId + "&adminId=" + userObj.adminId + "&sendClient=" + this.checkedgroup_num + "&sendServer=" + this.checkedservice_num).then((response) => {
						console.log(response.body)
					})
				}

			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/css/var.scss';
	.onlive {
		border: 1px solid $bordercolor;
		color: $txtcolor;
		padding: 0 $font16;
		background-color: $whitecolor;
		label {
			display: block;
			
			font-size: $font14;
		}
		input {
			border: none;
			font-size: $font14;
			width: 80%;
			padding:20px 0;
			margin: 0;
			
		}
		[for="title"] {
			border-bottom: 1px solid $bordercolor;
		}
	}
	
	.sendButton {
		height: 50px;
		line-height: 50px;
		color: #fff;
		text-align: center;
		font-size: $font18;
		background-color: $bluecolor;
		width: 90%;
		margin: auto;
		margin-top: 10px;
		border-radius: 3px;
	}
	
	.choosecheck {
		height: 40px;
		line-height: 40px;
		margin-left: 5%;
	}
</style>


