<template>
	<div class="login">
		<div class="login_div">
			<i class="iconfont icon-shouji"></i>
			<input type="text" placeholder="请输入手机号" v-model="phone" />
		</div>
		<div class="login_div">
			<i class="iconfont icon-mima"></i>
			<input type="password" placeholder="请输入密码" v-model="password" />
		</div>
		<p class="login_button" @click="login">登录</p>
		<p class="reg">没有账号？
			<router-link :to="'register'">马上注册</router-link>
		</p>
	</div>

</template>

<script>
	import { Toast } from 'mint-ui';
	
	export default {
		name: 'login',
		data() {
			return {
				phone: "",
				password: ""
			}
		},
		mounted() {
			//读取本地微信openid 有的话直接跳转 进入首页home
			//没有的话 请求获取openid 登录页面  信息包括openid一起提交服务器
			//var wxopenid="oWk-fuP7uqLUJcoX118Mt6oWyFZo";
//			localStorage.setItem("openId" ,"oWk-fuP7uqLUJcoX118Mt6oWyFZo");
			var wxopenid = localStorage.getItem("openId");
			if(wxopenid != undefined) {
				console.log("跳转home页面");
				this.$router.push('home');
				this.$http.get("http://120.24.6.91:8082/wx/powderSystem/virUserByOpenId?openid=" + wxopenid).then((response) => {
					console.log(response.body);
					localStorage.setItem("nickname", response.body.nickname);
					localStorage.setItem("city", response.body.city);
					localStorage.setItem("country", response.body.country);
					localStorage.setItem("openid", response.body.openid);
					localStorage.setItem("uid", response.body.uid)
					localStorage.setItem("province", response.body.province)
					localStorage.setItem("sex", response.body.sex);
					localStorage.setItem("unionid", "");
					
					//成为全局使用
					userObj.nickname=response.body.nickname;
					userObj.uid=response.body.uid;
					userObj.headimgurl=response.body.headimgurl;
				
				});
			} else {
				//假装获取 到 openid  

				var wxopenid = "oyecBuKbF1seYxcWQOW5HapiKVHw";
				//如果为空 跳转登录页面

				console.log("跳转登录页面");
				this.$router.push('login');

			}

			function isEmptyObject(obj) {
				for(var key in obj) {
					return false;
				}
				return true;
			}
		},
		methods: {
			login() {
//				http://120.24.6.91:8082/wx /powderSystem/wxUserbind
//
//	tel				手机号码	
//	pwd				密码
//	openid			OpenId
//	nickname			微信昵称
//	country			市级
//	province			省级
//	city				县
//	headimgurl		头像地址
//	sex				性别
//	unionid			(先空)
this.$http.get("http://120.24.6.91:8082/wx/powderSystem/wxUserbind?tel="+this.phone+
"&pwd="+this.password+"&openid=" +localStorage.getItem("openid")+
"&nickname="+ localStorage.getItem("nickname")+
"&country="+ localStorage.getItem("country")+
"&province=" +localStorage.getItem("province")+
"&city="+ localStorage.getItem("city")+
"&headimgurl=" +localStorage.getItem("headimgurl")+
"&sex="+ localStorage.getItem("sex")+
"&unionid="+ localStorage.getItem("unionid")).then((response) =>{
	console.log(response.body)
	if(response.body.errorcode == 404){
		Toast({
						message: '账号密码错误',
						position: 'middle',
						duration: 1000
					});
	}else{
		
		this.$router.push("home");
	}
});

			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/css/var.scss';
	.login {
		position: relative;
		top: 130px;
		height: calc(100vh - 130px);
		div.login_div {
			height: 52px;
			line-height: 52px;
			width: 70%;
			margin: auto;
			background-color: $whitecolor;
			border: 1px solid $barbg;
			margin-bottom: 15px;
			border-radius: 2px;
			input {
				border: none;
				padding: 0;
				margin: 0;
				vertical-align: top;
				height: 50px;
				line-height: 50px;
				width: 70%;
				font-size: $font16;
				/*	 background: #00f;*/
			}
			 :-ms-input-placeholder {
				color: $pagebg;
			}
			i {
				font-size: 25px;
				padding: 0 8px;
			}
		}
		p.login_button {
			height: 52px;
			line-height: 52px;
			text-align: center;
			color: $bluecolor;
			background-color: $whitecolor;
			width: 62%;
			margin: auto;
			border-radius: 2px;
			font-size: $font16;
		}
		p.reg {
			position: absolute;
			bottom: 20px;
			width: 100%;
			text-align: center;
			i {
				color: $bluecolor;
				text-decoration: underline;
			}
		}
	}
</style>


